'use client'

import { createContext, useContext, useEffect, useState } from 'react'
import { useSession, signIn, signOut } from 'next-auth/react'
import { useRouter } from 'next/navigation'
import { apiClient } from '@/lib/api-client'

interface User {
  id: string
  email: string
  full_name: string
  user_type: 'patient' | 'doctor' | 'hospital'
  avatar_url?: string
  phone?: string
}

interface AuthContextType {
  user: User | null
  loading: boolean
  signInWithGoogle: () => void
  signInWithEmail: (email: string, password: string) => Promise<{ success: boolean; error?: string }>
  signUpWithEmail: (userData: any) => Promise<{ success: boolean; error?: string }>
  logout: () => void
  updateProfile: (updates: Partial<User>) => Promise<{ success: boolean; error?: string }>
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const { data: session, status } = useSession()
  const [user, setUser] = useState<User | null>(null)
  const [loading, setLoading] = useState(true)
  const router = useRouter()

  useEffect(() => {
    if (status === 'loading') {
      setLoading(true)
      return
    }

    if (session?.user) {
      // Convert NextAuth session to our User type
      setUser({
        id: session.user.userId || '',
        email: session.user.email || '',
        full_name: session.user.fullName || session.user.name || '',
        user_type: session.user.userType || 'patient',
        avatar_url: session.user.image || '',
      })
    } else {
      setUser(null)
    }

    setLoading(false)
  }, [session, status])

  const signInWithGoogle = () => {
    signIn('google', { callbackUrl: '/dashboard' })
  }

  const signInWithEmail = async (email: string, password: string) => {
    try {
      const result = await apiClient.users.signIn({ email, password })
      
      if (result.success && result.data) {
        setUser(result.data.profile)
        router.push(`/${result.data.profile.user_type}-dashboard`)
        return { success: true }
      }
      
      return { success: false, error: result.error }
    } catch (error) {
      return { success: false, error: 'Sign in failed' }
    }
  }

  const signUpWithEmail = async (userData: any) => {
    try {
      const result = await apiClient.users.signUp(userData)
      
      if (result.success && result.data) {
        setUser(result.data.profile)
        router.push(`/${result.data.profile.user_type}-dashboard`)
        return { success: true }
      }
      
      return { success: false, error: result.error }
    } catch (error) {
      return { success: false, error: 'Sign up failed' }
    }
  }

  const logout = () => {
    signOut({ callbackUrl: '/' })
    setUser(null)
  }

  const updateProfile = async (updates: Partial<User>) => {
    if (!user) return { success: false, error: 'No user logged in' }

    try {
      const result = await apiClient.users.updateProfile(user.id, updates)
      
      if (result.success && result.data) {
        setUser({ ...user, ...result.data })
        return { success: true }
      }
      
      return { success: false, error: result.error }
    } catch (error) {
      return { success: false, error: 'Profile update failed' }
    }
  }

  return (
    <AuthContext.Provider value={{
      user,
      loading,
      signInWithGoogle,
      signInWithEmail,
      signUpWithEmail,
      logout,
      updateProfile,
    }}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}